import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App'
import {initRouter, commonRouter} from './routerMap'

Vue.use(VueRouter)

let router = new VueRouter()

initRouter(router)

commonRouter()

router.redirect({
  '*': '/login'
})

router.start(App, '#app')



