import Login from './components/Login.vue'
import ChooseNetWork from './components/ChooseNetWork.vue'
import Other from './components/Other.vue'

var finalRouter = false

function initRouter (router) {
  finalRouter = router
}

function commonRouter () {
  finalRouter.map({
    '/login': {
      component: Login
    },
    '/choose': {
      component: ChooseNetWork
    },
    '/other': {
      component: Other
    }
  })
}
function onRouter (path, component) {
  finalRouter.on(path, {
    component: component
  })
}

export {initRouter, commonRouter, onRouter}



